body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}body #root{min-height:100vh;height:100%;display:flex;flex-direction:column;justify-content:space-between}header{height:70px;display:flex;align-items:center;justify-content:center;color:white;background:linear-gradient(#51a6e7, #73c2ff)}header h2{display:flex;align-items:center}header h2 img{margin:0 8px;max-height:35px}.loader{width:100%;height:100%;display:flex;align-items:center;flex-grow:1}.loader .sk-chase{margin-right:auto;margin-left:auto;width:50px;height:50px;position:relative;animation:sk-chase 2.5s infinite linear both}.loader .sk-chase .sk-chase-dot{width:100%;height:100%;position:absolute;left:0;top:0;animation:sk-chase-dot 2s infinite ease-in-out both}.loader .sk-chase .sk-chase-dot:before{content:'';display:block;width:25%;height:25%;background-color:#73c2ff;border-radius:100%;animation:sk-chase-dot-before 2s infinite ease-in-out both}.loader .sk-chase .sk-chase-dot:nth-child(1){animation-delay:-1.1s}.loader .sk-chase .sk-chase-dot:nth-child(2){animation-delay:-1s}.loader .sk-chase .sk-chase-dot:nth-child(3){animation-delay:-0.9s}.loader .sk-chase .sk-chase-dot:nth-child(4){animation-delay:-0.8s}.loader .sk-chase .sk-chase-dot:nth-child(5){animation-delay:-0.7s}.loader .sk-chase .sk-chase-dot:nth-child(6){animation-delay:-0.6s}.loader .sk-chase .sk-chase-dot:nth-child(1):before{animation-delay:-1.1s}.loader .sk-chase .sk-chase-dot:nth-child(2):before{animation-delay:-1s}.loader .sk-chase .sk-chase-dot:nth-child(3):before{animation-delay:-0.9s}.loader .sk-chase .sk-chase-dot:nth-child(4):before{animation-delay:-0.8s}.loader .sk-chase .sk-chase-dot:nth-child(5):before{animation-delay:-0.7s}.loader .sk-chase .sk-chase-dot:nth-child(6):before{animation-delay:-0.6s}@keyframes sk-chase{100%{transform:rotate(360deg)}}@keyframes sk-chase-dot{80%,100%{transform:rotate(360deg)}}@keyframes sk-chase-dot-before{50%{transform:scale(0.4)}100%,0%{transform:scale(1)}}footer{display:flex;justify-content:center;color:white;background-color:#2c2c2c}main{text-align:center;font-size:1.2rem;padding:0;flex-grow:1;max-width:100vw;display:flex;flex-direction:column}@media (min-width: 770px){main{padding:0 5% 1rem}}main iframe{flex-grow:1;border:none}@media (min-width: 770px){main iframe{border-radius:4px}}main .MuiButtonBase-root{font-family:inherit;color:white;background-color:#73c2ff}main .MuiButtonBase-root:hover{background-color:#51a6e7}
